/* Basic Reset */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  background-color: #f0f2f5;
}

.App {
  text-align: left;
}

/* Dashboard spacing */
h2 {
  margin-bottom: 20px;
  color: #333;
}
